.App {
  text-align: center;
  background-color: var(--background-color);
}

.App a {
  color: var(--link-color);
}

.light {
  --aboutme-background-color: rgb(236, 237, 239);
  --techstackpage-background-color: rgb(16, 16, 26);
  --skillspage-background-color: rgb(236, 237, 239);
  --statisticspage-background-color: rgb(16, 16, 26);
  --scroll-top-background-color: teal;
  --techstack-text-color: white;
  --projectfeature-text-color: rgba(16, 24, 52, 0.75);
  --statisticspage-text-color: gray.600;
  --formheading-text-color: rgb(16, 24, 52);
  --form-text-color: rgba(16, 24, 52, 0.75);
  --forminput-text-color: green;
  --desktop-nav-text-color: rgb(153, 24, 24);
  --desktop-icon-color: rgb(153, 24, 24);
  --scroll-border-color: 2px solid teal;
}

.dark {
  /* --text-color: rgb(153, 24, 24); */
  /* --link-color: #a39e1c; */
  --link-color: rgb(33, 232, 15);
  --background-color: #23283e;
  --aboutme-background-color: #23283e;
  --techstackpage-background-color: #23283e;
  --statisticspage-background-color: #23283e;
  --scroll-top-background-color: rgb(237, 50, 55);
  --text-color: #bdbddd;
  --projectheading-text-color: #90a0d9;
  --techstack-text-color: #bdbddd;
  --projectfeature-text-color: #bdbddd;
  --statisticspage-text-color: #bdbddd;
  --formheading-text-color: #90a0d9;
  --form-text-color: gray;
  --forminput-text-color: orange;
  --desktop-nav-text-color: #13a77d;
  --desktop-icon-color: #13a77d;
  --text-color-alt: #13a77d;
  --text-primary: #90a0d9;
  --scroll-border-color: 2px solid rgb(237, 50, 55);
}

.App .navdark {
  background-color: orange;
}

.App .icon {
  color: var(--desktop-icon-color);
}
.text {
  color: var(--text-color);
}

.App .aboutmepage {
  background-color: var(--aboutme-background-color);
}

.aboutmepage .text {
  color: var(--text-color);
}

.App .techstackpage {
  background-color: var(--techstackpage-background-color);
}

.App .techstacktextcolor {
  color: var(--techstack-text-color);
}
.App .skillspage {
  background-color: var(--skillspage-background-color);
}

.skillspage .text {
  color: var(--text-color);
}

.App .projectheading {
  color: var(--projectheading-text-color);
}

.App .projectfeature {
  color: var(--projectfeature-text-color);
}

.App .statisticspage {
  background-color: var(--statisticspage-background-color);
}

.statisticspage .text {
  color: var(--statisticspage-text-color);
}

.github {
  color: var(--text-color);
}

.App .scroll {
  color: var(--scroll-top-background-color);
  border: var(--scroll-border-color);
}

.App .formheading {
  color: var(--formheading-text-color);
}

.App .form {
  color: var(--form-text-color);
}

.App .forminput {
  color: var(--forminput-text-color);
  font-size: larger;
}

.App .submit {
  background-color: firebrick;
  color: whitesmoke;
  letter-spacing: 0.09rem;
  font-family: Arial, Helvetica, sans-serif;
}

.App .home {
  color: var(--desktop-nav-text-color);
}

.App .about {
  color: var(--desktop-nav-text-color);
}

.App .projects {
  color: var(--desktop-nav-text-color);
}

.App .techstacks {
  color: var(--desktop-nav-text-color);
}

.App .skills {
  color: var(--desktop-nav-text-color);
}

.App .contact {
  color: var(--desktop-nav-text-color);
}
